* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
  font-size: 1.3rem;
}

button {
  background-color: #09f;
  border-radius: 10px;
  border: none;
  color: white;
  font-family: inherit;
  font-size: inherit;
  padding: 10px;
}

input {
  color: grey;
  font-size: 1.2rem;
  padding: 10px;
  width: 100%;
}

.card {
  align-content: center;
  border-radius: 6px;
  border: 1px solid grey;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card figcaption {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

.card__grid {
  display: flex;
  flex-wrap: wrap;
}
